.gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    background-color: rgba(43, 46, 53, 0.7);
    padding: 3%;
}

.gallery-image {
    width: calc(33.33% - 20px);
    /* Adjust width as needed */
    max-width: 300px;
    /* Adjust maximum width as needed */
    margin-bottom: 10px;
}