.about-container {
    background-color: rgba(43, 46, 53, 0.7);
    color: white;
    padding: 3%;
}

.manager {
    background-color: rgba(43, 46, 53, 0.7);
    color: white;
    padding: 2% 3% 0 3%;
}

.message {
    width: 70%;
    text-align: initial;
    margin-bottom: 2%;
}

.ManagerPhoto {
    background-image: url(./Assets/AboutUS/Manager.png);
    background-size: cover;
    height: auto;
    width: 235px;
    margin: 0 auto;
}

.Awards {
    padding: 2%;
    background-color: rgba(212, 209, 202, 1);
}

.Awards h3 {
    margin-bottom: 3%;
}

.AwardDetail {
    margin: 0 2%;
}

.AwardsBlock {
    display: flex;
    justify-content: space-around;
}