/* Resetting default margin and padding */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: Arial, sans-serif;
  }
  
  .portfolio-footer {
    background-color: rgb(212, 209, 202);
    padding: 50px 0;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .firm-info {
    max-width: 50%;
  }
  
  .firm-info h2 {
    margin-bottom: 10px;
  }
  
  .newsletter-card {
    max-width: 40%;
    background-color: rgba(43, 46, 53);
    color: #fff;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .newsletter-card h3 {
    margin-bottom: 10px;
  }
  
  .newsletter-card form {
    display: flex;
  }
  
  .newsletter-card input[type="email"] {
    flex: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px 0 0 5px;
  }
  
  .newsletter-card button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
  }
  
  .newsletter-card button:hover {
    background-color: #0056b3;
  }
  