.card-slider {
    display: flex;
    /* flex-direction: column; */
    align-items: center;
    justify-content: center;
}

.cards-container {
    display: flex;
    justify-content: space-between;
    /* width: 400px; */
    /* Adjusted width to accommodate 3 cards */
    overflow: hidden;
}

.card {
    width: 400px;
    height: 250px;
    margin: 2%;
    background-color: #eee;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #ccc;
    transition: transform 0.5s;
}

.card.active {
    transform: scale(1.1);
}

button {
    margin-top: 10px;
}

.controlButton {
    background: none;
    border: none;
    color: white;
    font-size: 70px;
}